.app-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
}

main {
  -webkit-flex: 1 1;
          flex: 1 1;
}

nav {
  -webkit-flex: 0 0 12em;
          flex: 0 0 12em;
  -webkit-order: -1;
          order: -1;
  min-height: 100vh;
}

nav ul {
  margin: 0.5em;
  border: 1px solid #eeeeee;
  border-radius: 3px;
  padding: 0;
  list-style-type: none;
}

nav ul li {
  font-size: 0.8em;
  font-weight: 600;
  border-bottom: 1px solid #eeeeee;
}

nav ul li:first-of-type a {
  border-top-right-radius: 2px;
}
nav ul li:last-of-type a {
  border-bottom-right-radius: 2px;
}
nav ul li:last-of-type {
  border-bottom: none;
}

nav ul li a {
  display: block;
  text-decoration: none;
  color: #666666;
  padding: 0.4em;
}
nav ul li a.selected {
  border-right: 2px solid #333944;
  background-color: #eeeeee;
}
.fixed-size-holder {
    width: 500px;
    height: 400px;
}
.padded-page {
    padding: 1em;
}
form.controls {
    display: inline-block;
    margin-bottom: 1em;
    padding: 0.2em;
    background-color: hsl(220, 75%, 90%);
    border: solid 1px hsl(220, 100%, 40%);
    border-radius: 2px;
}

form.controls .inline-controls-group {
    margin-right: 1em;
}
form.controls .inline-controls-group:last-of-type {
    margin-right: 0;
}

form.controls input[type="number"] {
    width: 4em;
}
.event-log {
    width: 100%;
    height: 6em;
    font-family: 'Courier New', Courier, monospace;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  font-size: 0.9em;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 2px;
  color: rgb(196, 78, 78);
}

